*{
 padding:0;
 margin: 0;
 box-sizing: border-box;

}

body {
 font-family: "Montserrat";
 color: rgb(218,158,53);


}

section {
 background-image: url("../images/votebackgroung.png");
 background-size: cover;
min-height: 100vh;

 max-width: 1170;
 display: flex;
 align-items: center;
 justify-content: center;
 flex-direction: column;

}
section .main {
  display: inline-block;
  justify-content: center;
  align-items: center;
  flex-direction: column;


}
section .main h2 {
  font-weight: 500;

  font-size: 20px;
  line-height: 40px;
}

.container {
 width: 90%;
 max-width: 500px;
 margin: 0;
 padding: 20px;
 background: linear-gradient(rgba(0,0,0.4),rgba(0,0,0.6));

 border-radius: 8px;
 margin-bottom: 20px;
}
.form-group h1, p{
 color: rgb(189,167,191);
}
.form-group {
 width: 100%;
 margin-top: 20px;
 font-size: 20px;
}
.form-group input, .form-group textarea {
 width: 100%;
 padding: 5px;
 font-size: 18px;
 border: 1px solid;
 margin-top: 5px;
 background: linear-gradient(rgba(0,0,0.4),rgba(0,0,0.6));
 border-color: rgb(219,233,162);
 color: white;
}
textarea {
 resize: vertical;
}
button[type="submit"] {
 width: 100%;
 border: none;
 outline: none;
 padding: 20px;
 font-size: 24px;
 border-radius: 8px;
 font-family: "Montserrat";
 color:black;
 text-align: center;
 cursor: pointer;
 margin-top: 10px;
 background-color: rgb(189,167,191);
