}
#status {
 width: 90%;
 max-width: 500px;
 text-align: center;
 padding: 10px;
 margin: 0 auto;
 border-radius: 8px;
 color: black;
}
#status.success {
 background-color: rgb(219, 233, 162);


 animation: status 4s ease forwards;
}
#status.error {
 background-color: rgb(247, 49, 14);
 color: white;
 animation: status 4s ease forwards;
}
@keyframes status {
 0% {
   opacity: 1;
   pointer-events: all;
 }
 90% {
   opacity: 1;
   pointer-events: all;
 }
 100% {
   opacity: 0;
   pointer-events: none;
 }
}
@media(min-width:700px) {
 section {
     background-image: url("../images/synth_13.png");

 }
}
